import React from "react";
import M from "materialize-css";
import GoogleLogin from "react-google-login";
import { GoogleLogout } from "react-google-login";
import AddAlbum from "./AddAlbum.js";
import AddPhoto from "./AddPhoto.js";
import logo from "./default.png";
import office from "./office.jpg";
import axios from "axios";

const modalOptions = {
  opacity: 0,
};

class NavBar extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoggedIn: false,
      profilePicture: logo,
      name: "",
      email: "",
      addAlbumModal: null,
      addPhotoModal: null,
      token: "",
      albums: [],
    };

    this.createAlbumRef = React.createRef();
    this.addPhotosRef = React.createRef();
  }

  closeModals = () => {
    this.state.addAlbumModal.close();
    this.state.addPhotoModal.close();
  };

  componentDidMount() {
    this.createAlbumRef.current.classList.toggle("disabled");
    this.addPhotosRef.current.classList.toggle("disabled");

    const elem2 = document.getElementById("modal2");
    const modal = M.Modal.init(elem2, modalOptions);

    const elem3 = document.getElementById("modal3");
    const modal2 = M.Modal.init(elem3, modalOptions);

    this.setState({
      addAlbumModal: modal,
      addPhotoModal: modal2,
    });

    const options = {
      onCloseStart: this.closeModals,
    };

    const elem = document.querySelectorAll(".sidenav");
    M.Sidenav.init(elem, options);
  }

  handleClickAddAlbum = () => {
    this.state.addAlbumModal.open();
  };

  handleClickAddPhoto = () => {
    this.state.addPhotoModal.open();

    axios
      .get("/albums", {
        params: { idtoken: this.state.token, email: this.state.email },
      })
      .then((resolve) => {
        //console.log(resolve);
        this.setState({ albums: resolve.data });
      });
  };

  onSuccess = (response) => {
    //console.log(response);
    const googleUser = response.getBasicProfile();
    const profile = googleUser.getImageUrl();
    const name = googleUser.getName();
    const email = googleUser.getEmail();

    this.setState({
      isLoggedIn: !this.state.isLoggedIn,
      profilePicture: profile,
      name: name,
      email: email,
      token: response.getAuthResponse().id_token,
    });

    this.createAlbumRef.current.classList.remove("disabled");
    this.addPhotosRef.current.classList.remove("disabled");
    this.props.getAuth({
      idtoken: this.state.token,
      email: this.state.email,
    });
  };

  onFailure = (response) => {
    console.log(response);
  };

  logout = () => {
    //console.log("Logged out");
    this.setState({
      isLoggedIn: !this.state.isLoggedIn,
      profilePicture: logo,
      name: "",
      email: "",
    });

    this.createAlbumRef.current.classList.toggle("disabled");
    this.addPhotosRef.current.classList.toggle("disabled");
    this.props.removeAuthCallBack();
  };

  render() {
    return (
      <div>
        <nav className="nav-extended">
          <div className="nav-wrapper">
            <a
			  href="#!"
              className="logo paris"
              onClick={this.handleHome}
              style={{ cursor: "pointer" }}
            >
              Stiny
            </a>
            <a
              href="#!"
              data-target="slide-out"
              className="sidenav-trigger left waves-effect"
            >
              <i
                style={{ fontSize: "300%", color: "black" }}
                className="material-icons"
              >
                menu
              </i>
            </a>
          </div>
        </nav>
        <ul id="slide-out" className="sidenav">
          <li>
            <div className="user-view">
              <div className="background">
                <img alt="office-background" src={office} />
              </div>
              <a href="#user">
                <img
                  alt="profile"
                  className="circle"
                  src={this.state.profilePicture}
                />
              </a>
              <a href="#name">
                <span className="white-text name">{this.state.name}</span>
              </a>
              <a href="#email">
                <span className="white-text email">{this.state.email}</span>
              </a>
            </div>
          </li>
          <li>
            <a
              ref={this.createAlbumRef}
              className="waves-effect"
              href="#!"
              disabled={!this.state.isLoggedIn}
              onClick={this.handleClickAddAlbum}
            >
              <i className="material-icons">book</i>Create Album
            </a>
            <AddAlbum token={this.state.token} email={this.state.email} />
          </li>
          <li>
            <a
              ref={this.addPhotosRef}
              className="waves-effect"
              href="#!"
              disabled={!this.state.isLoggedIn}
              onClick={this.handleClickAddPhoto}
            >
              <i className="material-icons">insert_photo</i>Add Photos
            </a>
            <AddPhoto
              token={this.state.token}
              email={this.state.email}
              albumList={this.state.albums}
			  setAlbumCallBack={this.props.setAlbumCallBack}
			  album={this.props.album}
            />
          </li>
          <li>
            <div className="divider"></div>
          </li>
          <li>
            <GoogleLogin
              className="google-button"
              clientId={process.env.REACT_APP_CLIENT_ID}
              buttonText="Login"
              onSuccess={this.onSuccess}
              onFailure={this.onFailure}
              cookiePolicy={"single_host_origin"}
              disabled={this.state.isLoggedIn}
            />
            <GoogleLogout
              className="google-button"
              clientId={process.env.REACT_APP_CLIENT_ID}
              buttonText="Logout"
              onLogoutSuccess={this.logout}
              disabled={!this.state.isLoggedIn}
            ></GoogleLogout>
          </li>
          <li>
            <a href="#!" className="subheader">
              Built With Materialize CSS
            </a>
          </li>
        </ul>
      </div>
    );
  }
}

export default NavBar;

