import React from 'react';

class SearchBar extends React.Component{
  constructor(props) {
	super(props);
	this.myRef = React.createRef();
	this.spanRef = React.createRef();
  }

  handleKeyDown = e => {
	if (e.key === 'Enter') {
	  this.props.setAlbum(this.myRef.current.value);
	  //this.myRef.current.blur();
	} else {
	  this.props.clearCallback();
	}
  }

  render() {
    return (
      <div className="row center">
        <div className="input-field col s12">
          <input placeholder="e.g. Album 1" ref={this.myRef} id="search1" type="text" onKeyDown={this.handleKeyDown} />
          <label className="active" htmlFor="search1">Search</label>
		  <span className="helper-text" ref={this.spanRef}>{this.props.searchStatus}</span>
        </div>
      </div>
    );
  }
}

export default SearchBar;
